<template>
  <div class="post">
    <div class="cms-content" v-for="item in items" :key="item.id">
      <router-link to="/"><h3>Back to home</h3></router-link>
      <img class="cms-image" :src="`https://www.butor-mirjan24.hu/img/cms/inspirations/${item.id}.jpg`">
      <div class="cms-description"><span v-html="item.content"></span></div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Post',
  data() {
    return {
      link: 'https://www.butor-mirjan24.hu/api/content_management_system',
      token: '9U9NSIVVWWSWUKATK942H4BPC2QWXC3M',
      cms: 3,
      items: [],
    }
  },
  mounted() {
    var postID = document.querySelector('.post').getAttribute('id');
    axios
      .get(`${this.link}/${postID}?ws_key=${this.token}&output_format=JSON`)
      .then(resp => {
        this.items.push(resp.data.content);
      })
  },
}
</script>

<style scoped>
  .cms-content {
    max-width: 75vw;
    background-color: #eee;
    margin: 0 auto;
    padding: 20px;
  }
  .cms-image {
    margin: 0 auto;
    text-align: center;
    width: 100%;
  }
</style>
